"use client"
import React from 'react';
import { useState } from 'react';
import { FaChevronDown, FaBars, FaTimes } from 'react-icons/fa';
import Image from 'next/image';

export default function Navbar() {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(null);

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDropdownOpen(null);
  };

  const toggleDropdown = (menu) => {
    setDropdownOpen(dropdownOpen === menu ? null : menu);
  };

  return (
    <div className="Navbar-main">
      {/* Top Strip */}
      <div className="top-strip py-3 px-6 md:px-40 flex justify-between border-b text-xs bg-gray-100 text-black">
        <h1 >Timings : 24 / 7</h1>
        <p>info@analogueitsolutions.com</p>
      </div>

      {/* Navbar */}
      <div className="Navbar bg-white z-50 relative py-2 mt-2 shadow-md">
        <div className="flex justify-between items-center px-4 md:px-10">
          {/* Logo */}
          <img src="/Analogue-website-logo.png" alt="Logo" className="h-10 md:h-12" />

          {/* Desktop Menu */}
          <ul className="hidden lg:flex items-center gap-6 text-black">
            <li className="hover:text-blue-600 cursor-pointer">Home</li>
            <li className="hover:text-blue-600 cursor-pointer">About</li>

            {/* Services Dropdown */}
            <li className="relative group cursor-pointer">
              <div className="flex items-center gap-1">
                Services
                <FaChevronDown className="transition-transform duration-200 group-hover:rotate-180" size={12} />
              </div>
              <ul className="absolute top-full left-0 mt-2 w-48 bg-white border shadow-lg hidden group-hover:block z-50">
                <li className="px-4 py-2 hover:bg-gray-100">App Development</li>
                <li className="px-4 py-2 hover:bg-gray-100">Web Design</li>
                <li className="px-4 py-2 hover:bg-gray-100">Marketing</li>
              </ul>
            </li>

            {/* Technologies Dropdown */}
            <li className="relative group cursor-pointer">
              <div className="flex items-center gap-1">
                Technologies
                <FaChevronDown className="transition-transform duration-200 group-hover:rotate-180" size={12} />
              </div>
              <ul className="absolute top-full left-0 mt-2 w-48 bg-white border shadow-lg hidden group-hover:block z-50">
                <li className="px-4 py-2 hover:bg-gray-100">React</li>
                <li className="px-4 py-2 hover:bg-gray-100">Next.js</li>
                <li className="px-4 py-2 hover:bg-gray-100">Node.js</li>
              </ul>
            </li>

            {/* Our Products Dropdown */}
            <li className="relative group cursor-pointer">
              <div className="flex items-center gap-1">
                Our Products
                <FaChevronDown className="transition-transform duration-200 group-hover:rotate-180" size={12} />
              </div>
              <ul className="absolute top-full left-0 mt-2 w-48 bg-white border shadow-lg hidden group-hover:block z-50">
                <li className="px-4 py-2 hover:bg-gray-100">CRM</li>
                <li className="px-4 py-2 hover:bg-gray-100">HRMS</li>
                <li className="px-4 py-2 hover:bg-gray-100">E-commerce</li>
              </ul>
            </li>

            <li className="hover:text-blue-600 cursor-pointer">Blog</li>
            <li className="hover:text-blue-600 cursor-pointer">Contact</li>
          </ul>

          {/* Contact + Button */}
          <div className="hidden lg:flex items-center gap-4">
            <Image src="/phone-icon.png" alt="Phone" width={16} height={10} />
            <div className="flex flex-col">
              <span className="text-xs text-gray-500">Quick Call</span>
              <span className="text-xs text-blue-700">+91 8919088163</span>
            </div>
            <button className="bg-blue-600 text-sm text-white px-3 py-1.5 rounded-full hover:bg-blue-700 transition">
              Free Quote
            </button>
          </div>

          {/* Hamburger Icon */}
          <button className="lg:hidden" onClick={toggleSidebar}>
            <FaBars size={24} />
          </button>
        </div>

        {/* Sidebar */}
        {sidebarOpen && (
          <div className="fixed inset-0 bg-black bg-opacity-50 z-50">
            <div className="fixed top-0 left-0 w-64 h-full bg-white shadow-lg p-6 overflow-y-auto">
              <div className="flex justify-between items-center mb-6">
                <img src="Main-logo.png" alt="Logo" className="h-10" />
                <button onClick={toggleSidebar}><FaTimes size={20} /></button>
              </div>
              <ul className="flex flex-col gap-4 text-black">
                <li className="cursor-pointer">Home</li>
                <li className="cursor-pointer">About</li>

                <li>
                  <button
                    className="flex justify-between w-full items-center"
                    onClick={() => toggleDropdown('services')}
                  >
                    Services
                    <FaChevronDown size={12} className={`transition-transform ${dropdownOpen === 'services' ? 'rotate-180' : ''}`} />
                  </button>
                  {dropdownOpen === 'services' && (
                    <ul className="pl-4 mt-2">
                      <li className="py-1">App Development</li>
                      <li className="py-1">Web Design</li>
                      <li className="py-1">Marketing</li>
                    </ul>
                  )}
                </li>

                <li>
                  <button
                    className="flex justify-between w-full items-center"
                    onClick={() => toggleDropdown('tech')}
                  >
                    Technologies
                    <FaChevronDown size={12} className={`transition-transform ${dropdownOpen === 'tech' ? 'rotate-180' : ''}`} />
                  </button>
                  {dropdownOpen === 'tech' && (
                    <ul className="pl-4 mt-2">
                      <li className="py-1">React</li>
                      <li className="py-1">Next.js</li>
                      <li className="py-1">Node.js</li>
                    </ul>
                  )}
                </li>

                <li>
                  <button
                    className="flex justify-between w-full items-center"
                    onClick={() => toggleDropdown('products')}
                  >
                    Our Products
                    <FaChevronDown size={12} className={`transition-transform ${dropdownOpen === 'products' ? 'rotate-180' : ''}`} />
                  </button>
                  {dropdownOpen === 'products' && (
                    <ul className="pl-4 mt-2">
                      <li className="py-1">CRM</li>
                      <li className="py-1">HRMS</li>
                      <li className="py-1">E-commerce</li>
                    </ul>
                  )}
                </li>

                <li className="cursor-pointer">Blog</li>
                <li className="cursor-pointer">Contact</li>
              </ul>
              <div className="mt-6">
                <div className="flex items-center gap-3 mb-2">
                  <Image src="/phone-icon.png" alt="Phone" width={16} height={10} />
                  <div>
                    <p className="text-xs text-gray-500">Quick Call</p>
                    <p className="text-sm text-blue-700">+91 8919088163</p>
                  </div>
                </div>
                <button className="bg-blue-600 text-sm text-white px-3 py-1.5 rounded-full hover:bg-blue-700 transition">
                  Free Quote
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
